<script>

import { 
  authComputed, 
  } from "@/state/helpers";

export default {
  data() {
    return {
      pageSize: 10,
      pageOptions: [10, 25, 50, 100],
      pageNo: 1,
      totalPages: 0,
      // Filter Options
      searchQuery: '',
      sortDir: 'DESC',
      sortField: '',

    }
  },
  computed: {
    ...authComputed, 
    usersData () {
      return this.$store.state.userMgmt.users
    },    
    totalCount() {
      return this.$store.state.userMgmt.totalCount
    },
    currentPage() {
      return this.$store.state.userMgmt.currentPage
    },
    sortDesc() {
      return (this.sortDir==='DESC')
    }

  },
  methods: {
    handleSearch () {
      this.listUsers()
    },
    handleChangePage(page) {
      //let _print = `The user changed the page to: ${page}\n`
      console.log("handleChangePage")
      console.log(page)
      this.pageNo=page
      this.listUsers()
    },
    handleSort(key, active) {
      //let _print = `the user ordered: ${key} ${active}\n`
      this.sortDir=active?'ASC':'DESC'
      this.sortField=key
      this.listUsers()
    },
    listUsers() {
      this.$store.dispatch('userMgmt/fetchUsers', { 
        pageNo: this.pageNo, 
        pageSize: this.pageSize, 
        sortDir:this.sortDir,
        sortField:this.sortField | 'id',
        searchByOrMap:this.searchQuery?{ "userName":this.searchQuery, "loginId":this.searchQuery} :{},
      }).then(() => { 
        this.totalPages =this.$store.state.userMgmt.totalPages
      }).catch(err => { console.error(err) })

    },
    onRowClicked(item) {
      this.viewUser(item)
    },
    chatUser(userId) {
      this.$emit("chatUser", userId)
    }
  },
  mounted () {
    this.listUsers()
  }
}
</script>
<template>
