        </div> -->
        <ul class="list-unstyled chat-list">
          <!-- <li>
            <div
              class="align-self-center mr-3"
              v-if="item.avatarUrl"
            >
              <img
                :src="`${$utils.userImg(item.avatarUrl)}`"
                class="rounded-circle avatar-xs"
                alt
              />
              <a href="#">
                <h5 class="font-size-14 mb-0">{{ item.userName }} ({{ item.loginId }})</h5>
              </a>
            </div>
            <div
              class="avatar-xs align-self-center mr-3"
              v-if="!item.avatarUrl"
            >
              <span
                class="avatar-title rounded-circle bg-soft-primary text-primary"
                >{{ item.userName.charAt(0) }}</span
              >
              <a href="#">
                <h5 class="font-size-14 mb-0">{{ item.userName }} ({{ item.loginId }})</h5>
              </a>
            </div>
          </li> -->
          <li
            class
            @click="chatUser(item.id)" v-for="(item, index) in usersData" :key="index"
          >
            <a href="javascript: void(0);" v-if="item.id!==currentUser.id">
              <div class="media">
                <div
